<template>
  <div class="app flex-row align-items-center">
    <div class="container">
      <b-row class="justify-content-center">
        <b-col md="5">
          <div class="alert alert-danger" v-if="error">{{ error }}</div>
          <img src="static/img/logo.png" alt="Pizo Logo" class="img-center"><br><br>
          <!-- <b-card-group> -->
            <b-card no-body class="p-4">
              <b-card-body>
                <h1 class="">Log In</h1>
                <a href="/register" class="right">Register?</a>
                <p class="text-muted">Enter your credentials</p>
                <hr>
                <b-input-group class="mb-3">
                  <b-input-group-prepend>
                    <b-input-group-text>@</b-input-group-text>
                  </b-input-group-prepend>
                  <input v-model="email" type="email" class="form-control" placeholder="Email Address" required autofocus>
                </b-input-group>

                <b-input-group class="mb-3">
                  <div class="input-group-prepend">
                    <b-input-group-text><i class="fa fa-key"></i></b-input-group-text>
                  </div>
                  <input v-model="password" type="password" class="form-control" placeholder="Password" required>
                </b-input-group>

                <!-- <div class="checkbox mb-3 left">
                  <label class="">
                    <input v-model="rememberMe" checked="rememberMe" type="checkbox" value="remember-me"> Remember me
                  </label>
                </div> -->
                <div class="mb-3 right">
                  <label>
                    <a href="#" class="right" ><small>forgot password</small></a>
                  </label>
                </div>
                <button v-on:click.prevent="login" class="btn btn-lg btn-primary btn-block btn-pizo" type="submit">Sign in</button>
              </b-card-body>
            </b-card>
          <!-- </b-card-group> -->
        </b-col>
      </b-row>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Login',
  data () {
    return {
      email: '',
      password: '',

      error: null
    }
  },
  created () {
    document.title = 'Pizo - Login'
    document.head.querySelector('meta[name=description]').content = 'Pizo - Sign In Page'
    this.checkCurrentLogin()
  },
  updated () {
    this.checkCurrentLogin()
  },
  // computed: {
  //   ...mapGetters({
  //     token: 'token'
  //   })
  // },
  methods: {
    login () {
      console.log('logging in...')
      // login authentication
      this.$http.post('/users/signin', {email: this.email, password: this.password})
        .then((res) => {
          // successfull login
          localStorage.pizo_token = res.data.token
          console.log(res.data)
          // this.$router.replace('/admin')
          if (this.isAdmin()) {
            this.$router.replace('/admin')
          } else {
            this.$router.replace('/home')
          }
        })
        // catch errors
        .catch(() => this.loginFailed())
    },
    loginFailed () {
      this.error = 'Login failed! (wrong password or username)'
      // ensure deletion of token
      delete localStorage.pizo_token
    },
    checkCurrentLogin () {
      if (this.isLoggedIn()) {
        // if (this.isAdmin()) {
          this.$router.replace('/admin')
        // }
        // else {
        //   this.$router.replace('/home')
        // }
      }
    },
    isLoggedIn () {
      // redirect if logged in
      if (localStorage.pizo_token) {
        console.log('islogged in')
        return true
      } else {
        return false
      }
    },
    isAdmin () {
      if (this.email == 'admin@pizoeg.com') {
        return true
      } else {
        return false
      }
    }

  }
};
</script>

<style scoped>
  .left {
    float: left;
  }

  .right {
    float: right;
  }

  br {
    border: 1px;
    border-color: black;
  }

</style>



