<template>
  <div class="animated fadeIn">
    <!-- Alerts -->
    <b-alert  v-bind:variant="alertVariant"
              dismissible
              @dismissed="resetCompVars"
              v-bind:show="alertShow">{{ alertMessage }}</b-alert>
    <b-row>
      <div class="col-md-12">
        <!-- statistics -->
        <b-card-group deck>
          <b-card border-variant="primary"
                  header="Total Users"
                  align="center">
            <p class="card-text"><i class="text-primary fa fa-users"></i> {{ getStatisticsTotalUsers }}</p>
          </b-card>
          <b-card border-variant="primary"
                  header="Engineers"
                  align="center">
            <p class="card-text"><i class="text-primary fa fa-users"></i> {{ getStatisticsEngineers }}</p>
          </b-card>
          <b-card border-variant="primary"
                  header="Doctors"
                  align="center">
            <p class="card-text"><i class="text-primary fa fa-users"></i> {{ getStatisticsDoctors }}</p>
          </b-card>
          <b-card border-variant="primary"
                  header="Companies"
                  align="center">
            <p class="card-text"><i class="text-primary fa fa-building"></i> {{ getStatisticsCompanies }}</p>
          </b-card>
          <b-card border-variant="primary"
                  header="Products"
                  align="center">
            <p class="card-text"><i class="text-primary fa fa-shopping-cart"></i> {{ getStatisticsProducts }}</p>
          </b-card>
          <b-card border-variant="primary"
                  header="Orders"
                  align="center">
            <p class="card-text"><i class="text-primary fa fa-shopping-basket"></i> {{ getStatisticsOrders }}</p>
          </b-card>

        </b-card-group>
        <!-- ./statistics -->
      </div>
    </b-row>
    <hr>
    <!-- activate user modal -->
    <b-modal  id="activateUserModal"
              ref="activateUserModal"
              lazy
              title="Activate User(s)"
              ok-variant="success"
              ok-title="Activate"
              @ok="handleModalOkActivate">Confirm Activating Selected User(s)</b-modal>
    <!-- deactivate user modal -->
    <b-modal  id="deactivateUserModal"
              ref="deactivateUserModal"
              lazy
              title="Deactivate User(s)"
              ok-variant="warning"
              ok-title="Deactivate"
              @ok="handleModalOkDeactivate">Confirm Deactivating Selected User(s)</b-modal>
    <!-- delete user modal -->
    <b-modal  id="deleteUserModal"
              ref="deleteUserModal"
              lazy
              title="Delete User(s)"
              ok-variant="danger"
              ok-title="Delete"
              @ok="handleModalOkDelete">Confirm Deleting Selected User(s)</b-modal>
    <!-- all users area -->
    <b-row>
      <div class="col-md-12">
        <vue-good-table
          :columns="columns"
          :rows="rows"
          ref="usersTable"
          :selectOptions="{
            enabled: true,
            selectOnCheckboxOnly: true, // only select when checkbox is clicked instead of the row
            selectionInfoClass: 'custom-class',
            selectionText: 'user(s) selected',
            clearSelectionText: 'clear',
          }"
          :sort-options="{
            enabled: true,
            initialSortBy: {field: 'id', type: 'desc'}
          }"
          :search-options="{
            enabled: true,
          }"
          :pagination-options="{
            enabled: true,
            perPage: 20,
          }"
          styleClass="vgt-table striped bordered">

          <div slot="table-actions">
            <b-button variant="success" @click="handleActivateClick"><i class="fa fa-check"></i> Activate</b-button>
            <b-button variant="warning" @click="handleDeactivateClick"><i class="fa fa-times"></i> Deactivate</b-button>
            <b-button variant="danger" @click="handleDeleteClick"><i class="fa fa-trash"></i> Delete</b-button>
          </div>
          <template slot="table-row" slot-scope="props">
            <span v-if="props.column.field == 'status'">
              <span v-if="props.row.status == 0" style="font-weight: bold; color: Red;">Inactive</span>
              <span v-if="props.row.status == 1" style="font-weight: bold; color: Green;">Active</span>
            </span>
            <span v-if="props.column.field == 'group'">
              <span v-if="props.row.group == 1" style="font-weight: bold; color: Blue;">Admin</span>
              <span v-if="props.row.group == 2" style="font-weight: bold; color: Blue;">Engineer</span>
              <span v-if="props.row.group == 3" style="font-weight: bold; color: Blue;">Doctor</span>
              <span v-if="props.row.group == 4" style="font-weight: bold; color: Blue;">Company</span>
            </span>
            <span v-else>
              {{props.formattedRow[props.column.field]}}
            </span>
          </template>
        </vue-good-table>
      </div>
    </b-row>
    <!-- ./all users -->
  </div>
</template>

<script>
/*
  DASHBOARD:
    - Show all users (ID, Name, AccountType, Action(Activate/Deactivate))
    -
*/
export default {
  name: 'dashboard',
  created() {
    document.title = 'Pizo - Admin Panel';
    document.head.querySelector('meta[name=description]').content = 'Pizo - Admin Dashboard';
    this.fetchAllUsers()
  },
  data() {
    return {
      // componentVars
      alertVariant: '',
      alertMessage: '',
      alertShow: false,


      // status
      userStatus: {
        0: 'inactive',
        1: 'active'
      },

      // vue-good-table stuff
      columns: [
        { label: 'ID', field: 'id', type: 'number' },
        { label: 'Name', field: 'name', type: 'string', filterOptions: { enabled: true } },
        { label: 'Username', field: 'username', type: 'string', filterOptions: { enabled: true } },
        { label: 'Email', field: 'email', type: 'string', filterOptions: { enabled: true } },
        { label: 'Phone', field: 'phone', type: 'number', filterOptions: {enabled: true } },
        { label: 'Adress', field: 'address', type: 'string' },
        { label: 'Type', field: 'group', type: 'string' },
        { label: 'City', field: 'city', type: 'string' },
        { label: 'Created At', field: 'created_at' },
        { label: 'Status', field: 'status', type: 'string' }
      ],
      rows: [
      ]
    }
  },
  computed: {
    getStatisticsTotalUsers () {
      //TODO: total users
      var totalUsers = 0
      return totalUsers
    },
    getStatisticsEngineers () {
      //TODO:
      var engineers = 0
      return engineers
    },
    getStatisticsDoctors () {
      //TODO:
      var doctors = 0
      return doctors
    },
    getStatisticsCompanies () {
      //TODO:
      var companies = 0
      return companies
    },
    getStatisticsProducts () {
      //TODO:
      var products = 0
      return products
    },
    getStatisticsOrders () {
      //TODO:
      var orders = 0
      return orders
    }
  },
  methods: {
    fetchAllUsers () {
      this.$http.get('/admin/users' + '?token=' + localStorage.pizo_token)
        .then((response) => {
          // console.log(response.data.data)
          this.rows = response.data.data
        }).catch((error) => {
          this.handleError('Some error happened!')
          console.log(error.response)
        })
    },
    // fetchUsersEngineers () {
    //   var fetchedEngineers = []
    //   this.$http.get('/admin/users/2' + '?token=' + localStorage.pizo_token)
    //     .then((response) => {
    //       fetchedEngineers.push(response.data.data)
    //     }).catch((error) => {
    //       this.handleError('Some error happened!')
    //       console.log(error.response)
    //     })
    //   return fetchedEngineers
    // },
    // fetchUsersDoctors () {
    //   var fetchedDoctors = []
    //   this.$http.get('/admin/users/3' + '?token=' + localStorage.pizo_token)
    //     .then((response) => {
    //       fetchedDoctors.push(response.data.data)
    //     }).catch((error) => {
    //       this.handleError('Some error happened!')
    //       console.log(error.response)
    //     })
    //   return fetchedDoctors
    // },
    // fetchUsersCompanies () {
    //   var fetchedCompanies = []
    //   this.$http.get('/admin/users/3' + '?token=' + localStorage.pizo_token)
    //     .then((response) => {
    //       fetchedCompanies.push(response.data.data)
    //     }).catch((error) => {
    //       this.handleError('Some error happened!')
    //       console.log(error)
    //     })
    //   return fetchedCompanies
    // },
    getSelectedRowsIDs () {
      var selectedIDs = []
      var selectedRows = this.$refs['usersTable'].selectedRows
      for (var i = 0; i < selectedRows.length; i++) {
        var currentRow = selectedRows[i]
        selectedIDs.push(currentRow['id'])
      }
      return selectedIDs
    },
    showActivateUserModal () {
      this.$refs.activateUserModal.show()
    },
    showDeactivateUserModal () {
      this.$refs.deactivateUserModal.show()
    },
    showDeleteUserModal() {
      this.$refs.deleteUserModal.show()
    },
    handleActivateClick () {
      // checks before showing activate modal
      var selectedIDs = this.getSelectedRowsIDs()
      if (selectedIDs.length > 0) {
        this.showActivateUserModal()
      } else {
        this.handleError('You must select user(s) to activate')
      }
    },
    handleDeactivateClick () {
      // checks before showing deactivate modal
      var selectedIDs = this.getSelectedRowsIDs()
      if (selectedIDs.length > 0) {
        this.showDeactivateUserModal()
      } else {
        this.handleError('You must select user(s) to deactivate')
      }
    },
    handleDeleteClick () {
      // checks before showing delete modal
      var selectedIDs = this.getSelectedRowsIDs()
      if (selectedIDs.length > 0) {
        this.showDeleteUserModal()
      } else {
        this.handleError('You must select user(s) to delete')
      }
    },
    activateUser (userId) {
      this.$http.get('/admin/users/' + userId + '/activate?token=' + localStorage.pizo_token)
        .then((response) => {
          // this.handleSuccess(response.data.msg)
          console.log(response.data.msg)
        }).catch((error) => {
          console.log(error.response)
          this.handleError('Error while activating!')
        })
    },
    deactivateUser (userId) {
      this.$http.get('/admin/users/' + userId + '/deactivate?token=' + localStorage.pizo_token)
        .then((response) => {
            // this.handleSuccess(response.data.msg)
            console.log(response.data.msg)
          }).catch((error) => {
            console.log(error.response)
            this.handleError('Error while deactivating!')
          })
    },
    deleteUser (userId) {
      this.$http.get('/admin/users/' + userId + '/delete?token=' + localStorage.pizo_token)
        .then((response) => {
            // this.handleSuccess(response.data.msg)
            console.log(response.data.msg)
          }).catch((error) => {
            console.log(error.response)
            this.handleError('Error while deleting!')
          })
    },
    handleModalOkActivate () {
      var selectedIDs = this.getSelectedRowsIDs()
      if (selectedIDs.length > 0) {
        for (var i = 0; i < selectedIDs.length; i++) {
          this.activateUser(selectedIDs[i])
        }
        this.handleSuccess('User(s) activated')
      } else {
        this.handleError('Internal Error Happened!')
      }
      this.fetchAllUsers()
    },
    handleModalOkDeactivate () {
      var selectedIDs = this.getSelectedRowsIDs()
      if (selectedIDs.length > 0) {
        for (var i = 0; i < selectedIDs.length; i++) {
          this.deactivateUser(selectedIDs[i])
        }
        this.handleSuccess('User(s) deactivated')
      } else {
        this.handleError('Internal Error Happened!')
      }
      this.fetchAllUsers()
    },
    handleModalOkDelete () {
      var selectedIDs = this.getSelectedRowsIDs()
      if (selectedIDs.length > 0) {
        for (var i = 0; i < selectedIDs.length; i++) {
          this.deleteUser(selectedIDs[i])
        }
        this.handleSuccess('User(s) deleted')
      } else {
        this.handleError('Internal Error Happened!')
      }
      this.fetchAllUsers()
    },
    handleSuccess (message) {
      this.resetCompVars()
      this.alertShow = true
      this.alertVariant = 'success'
      this.alertMessage = message
    },
    handleError (message) {
      this.resetCompVars()
      this.alertShow = true
      this.alertVariant = 'danger'
      this.alertMessage = message
    },
    resetCompVars () {
      // reset components variables
      this.alertVariant = ''
      this.alertMessage = ''
      this.alertShow = false
    }
  }
}
</script>

<style scoped>

</style>



