  created () {
    this.checkCurrentLogin()
  },
  updated () {
    this.checkCurrentLogin()
  },
  methods: {
    checkCurrentLogin () {
      if (!localStorage.pizo_token && this.$route.path !== '/login') {
        this.$router.replace('/login')
      }
    },
    redirectUser () {
      if (localStorage.pizo_token) {
