<template>
  <header class="app-header navbar">
    <button class="navbar-toggler mobile-sidebar-toggler d-lg-none" type="button" @click="mobileSidebarToggle">
      <span class="navbar-toggler-icon"></span>
    </button>
    <b-link class="navbar-brand" to="#"></b-link>
    <button class="navbar-toggler sidebar-toggler d-md-down-none mr-auto" type="button" @click="sidebarToggle">
      <span class="navbar-toggler-icon"></span>
    </button>
    <!-- <button class="navbar-toggler aside-menu-toggler d-md-down-none" type="button" @click="asideToggle">
      <span class="navbar-toggler-icon"></span>
    </button> -->
    <!-- Logout button -->
    <button v-on:click.prevent="logout" class="btn btn-danger btn-outline-dark mr-3">
      <i class="fa fa-sign-out"></i> Logout</button>
  </header>
</template>
<script>

export default {
  name: 'c-header',
  methods: {
    sidebarToggle (e) {
      e.preventDefault()
      document.body.classList.toggle('sidebar-hidden')
    },
    sidebarMinimize (e) {
      e.preventDefault()
      document.body.classList.toggle('sidebar-minimized')
    },
    mobileSidebarToggle (e) {
      e.preventDefault()
      document.body.classList.toggle('sidebar-mobile-show')
    },
    // asideToggle (e) {
    //   e.preventDefault()
    //   document.body.classList.toggle('aside-menu-hidden')
    // }
    logout () {
      console.log("logging out...")
      // deleting token first from localStorage
      var tempToken = localStorage.pizo_token
      delete localStorage.pizo_token
      this.$http.get('/users/logout/', {
            headers: {
              'Authorization': ''
            },
            params: {token: tempToken}
        })
        .then((res) => {
          console.log(res.data.msg)
          this.$router.replace('/login')
        }).catch((error) => {
          console.log(error.response)
        })
    }
  }
}
</script>



