<template>
  <div class="app flex-row align-items-center">
    <div class="container">
      <b-row class="justify-content-center">
        <b-col md="5">
          <br>
          <b-alert v-if="created && !errorMessage" show variant="info">{{ created }}</b-alert>
          <b-alert v-else-if="errorMessage & !created" show variant="danger">{{ errorMessage }}</b-alert>
          <img src="static/img/logo.png" alt="Pizo Logo" class="img-center"><br><br>
          <b-card no-body class="p-4">
            <b-card-body>
              <h1>Register</h1>
              <a class="right" href="/login">already registerd?</a>
              <p class="text-muted">Create your account</p>
              <b-input-group class="mb-3">
                <b-input-group-prepend>
                  <b-input-group-text><i class="icon-user"></i></b-input-group-text>
                </b-input-group-prepend>
                <input type="text" class="form-control" placeholder="Username" v-model.lazy="username">
              </b-input-group>

              <b-input-group class="mb-3">
                <b-input-group-prepend>
                  <b-input-group-text>@</b-input-group-text>
                </b-input-group-prepend>
                <input type="text" class="form-control" placeholder="Email" v-model.lazy="email">
              </b-input-group>

              <b-input-group class="mb-3">
                <b-input-group-prepend>
                  <b-input-group-text><i class="icon-phone"></i></b-input-group-text>
                </b-input-group-prepend>
                <input type="tel" class="form-control" placeholder="01011111111" v-model.lazy="phone">
              </b-input-group>

              <b-input-group class="mb-3">
                <b-input-group-prepend>
                  <b-input-group-text><i class="icon-lock"></i></b-input-group-text>
                </b-input-group-prepend>
                <input type="password" class="form-control" placeholder="Password" v-model="password">
              </b-input-group>

              <b-input-group class="mb-3">
                <b-input-group-prepend>
                  <b-input-group-text><i class="icon-lock"></i></b-input-group-text>
                </b-input-group-prepend>
                <input type="password" class="form-control" placeholder="Repeat password" v-model="repeatPassword">
              </b-input-group>

              <b-input-group class="mb-3">
                <b-input-group-prepend>
                  <b-input-group-text>Account Type</b-input-group-text>
                </b-input-group-prepend>
                <b-form-select v-model="accountTypesSelected" v-bind:options="accountTypes">
                </b-form-select>
              </b-input-group>

              <b-button variant="success" block v-on:click.prevent="createAccount">Create Account</b-button>
            </b-card-body>
          </b-card>
        </b-col>
      </b-row>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Register',
  created() {
    document.title = 'Pizo - Register';
    document.head.querySelector('meta[name=description]').content = 'Pizo - Registeration Page';
  },
  data() {
    return {
      username: '',
      email: '',
      phone: '',
      password: '',
      repeatPassword: '',
      accountTypesSelected: null,
      accountTypes: [
        {value: null, text: 'select an option', disabled: true},
        {value: 2, text: 'Engineer'},
        {value: 3, text: 'Doctor'},
        {value: 4, text: 'Company'}
      ],


      created: null,
      failed: null,
      errorMessage: null,
    }
  },
  methods: {
    createAccount: function() {
      const url = 'https://pizoeg.com/api/v1/users/signup';
      const payload = {
        'name': null,
        'username': this.username,
        'email': this.email,
        'phone': this.phone,
        'address': null,
        'group': this.accountTypesSelected,
        'password': this.password,
        'city': null
      }

      this.$http.post(url, payload)
        .then((res) => {
          console.log(res.data)
          this.created = res.data.msg;
          }).catch((res) => {
            if (res.data.error == '1') {
              this.failed = true;
            }
            this.errorMessage = res.data.msg;
            console.log(res.data.msg);
          })
    },
    checkedInfo: function () {
      //TODO: check provided info before sending to the server
      passed = true;
      //actuall checking goes below
      return passed;
    }
  }

}
</script>

<style scoped>
 .img-center {
    /* margin-left: auto; */
    margin-right: auto;
  }
  img {
    margin-top: 20px;
  }
  .right {
    float: right;
  }
</style>



