<template>
  <div>
    <b-alert  v-bind:variant="alertVariant"
              dismissible
              @dismissed="resetCompVars"
              v-bind:show="alertShow">{{ alertMessage }}</b-alert>

    <!-- add product modal -->
    <b-modal  id="addProductModal"
              title="Add Product"
              ok-variant="primary"
              ok-title="Add"
              @ok="handleModalOkAddProduct">
      <!-- <form @submit.stop.prevent="handleSubmitAddPorduct"> -->
      <form>
        <b-form-group>Title:
          <b-form-input type="text"
                        placeholder="Item Name"
                        v-model="prodcutTitle"></b-form-input>
        </b-form-group>
        <b-form-group>
          Description:
          <b-form-textarea  id="prodcutDescription"
                            v-model="productDescription"
                            placeholder="Write full decription of the Item"
                            :rows="3"
                            :max-rows="6"></b-form-textarea>
        </b-form-group>
        <!-- disable image until implemented -->
        <!-- <b-form-group>
          Image: <b-form-file v-model="productImage" placeholder="Select Image from computer"></b-form-file>
        </b-form-group> -->
        <b-form-group>
          Price:
          <b-form-input placeholder="Enter Price (numbers only)"
                        type="number"
                        min="0"
                        max="250000"
                        v-model="productPrice"></b-form-input>
        </b-form-group>
        <b-form-group>
          Category:
          <b-form-select v-model="productCategory" :options="productCategoryOptions">
            <template slot="first">
              <!-- this slot appears above the options from 'options' prop -->
              <option :value="null" disabled selected>-- Select a category --</option>
            </template>
          </b-form-select>
        </b-form-group>
        <b-form-group>
          Specialization:
          <b-form-select v-model="productSpecialization" :options="productSpecializationOptions">
            <template slot="first">
              <!-- this slot appears above the options from 'options' prop -->
              <option :value="null" disabled selected>-- Select Specialization --</option>
            </template>
          </b-form-select>
        </b-form-group>
      </form>
    </b-modal>

    <!-- edit product modal -->
    <b-modal  id="editProductModal"
              ref="editProductModal"
              title="Edit Product"
              ok-variant="warning"
              ok-title="Edit"
              @ok="handleModalOkEditProduct">
      <form> <!-- edit product form -->
        <b-form-group>Title:
          <b-form-input type="text"
                        placeholder="Product Name"
                        v-model="prodcutTitleEdit"></b-form-input>
        </b-form-group>
        <b-form-group>
          Description:
          <b-form-textarea  id="prodcutDescription"
                            v-model="productDescriptionEdit"
                            placeholder="Write full decription of the product"
                            :rows="3"
                            :max-rows="6"></b-form-textarea>
        </b-form-group>
        <!-- diable image untile implemented -->
        <!-- <b-form-group>
          Image: <b-form-file v-model="productImageEdit" placeholder="Select Image from computer"></b-form-file>
        </b-form-group> -->
        <b-form-group>
          Price:
          <b-form-input placeholder="Enter Price (numbers only)"
                        type="number"
                        min="0"
                        max="250000"
                        v-model="productPriceEdit"></b-form-input>
        </b-form-group>
        <b-form-group>
          Category:
          <b-form-select v-model="productCategoryEdit" :options="productCategoryOptions">
            <template slot="first">
              <!-- this slot appears above the options from 'options' prop -->
              <option :value="null" disabled selected>-- Select a category --</option>
            </template>
          </b-form-select>
        </b-form-group>
        <b-form-group>
          Specialization:
          <b-form-select v-model="productSpecializationEdit" :options="productSpecializationOptions">
            <template slot="first">
              <!-- this slot appears above the options from 'options' prop -->
              <option :value="null" disabled selected>-- Select Specialization --</option>
            </template>
          </b-form-select>
        </b-form-group>
      </form>
    </b-modal>

    <!-- delete product modal -->
    <b-modal  id="deleteProductModal"
              ref="deleteProductModal"
              lazy
              title="Delete Product"
              ok-variant="danger"
              ok-title="Delete"
              @ok="handleModalOkDeleteProduct">Confirm Deleting Selected Product(s)</b-modal>
    <vue-good-table
      ref="productsTable"
      :columns="columns"
      :rows="rows"
      :selectOptions="{
        enabled: true,
        selectOnCheckboxOnly: true, // only select when checkbox is clicked instead of the row
        selectionInfoClass: 'custom-class',
        selectionText: 'products selected',
        clearSelectionText: 'clear'
      }"
      :sort-options="{
        enabled: true,
        initialSortBy: {field: 'id', type: 'desc'}
      }"
      :search-options="{
        enabled: true,
      }"
      :pagination-options="{
        enabled: true,
        perPage: 15,
      }"
      styleClass="vgt-table striped bordered">
      <div slot="table-actions">
        <b-button v-b-modal.addProductModal
          variant="primary"><i class="fa fa-plus"></i> Add Item</b-button>
        <b-button variant="warning" @click="handleEditClick"><i class="fa fa-edit"></i> Edit</b-button>
        <b-button variant="danger" @click="handleDeleteClick"><i class="fa fa-remove"></i> Delete</b-button>
      </div>
      <template slot="table-row" slot-scope="props">
        <span v-if="props.column.field == 'category_id'">
          <span v-if="props.row.category_id == '4'">category 1</span>
          <span v-else-if="props.row.category_id == '5'">category 2</span>
          <span v-else>category 3</span>
        </span>
        <span v-else>
          {{props.formattedRow[props.column.field]}}
        </span>
      </template>
      <div slot="emptystate">
        <p class="text-center">
          Trying get Products <br>
          Might be a Network problem.
        </p>
      </div>
    </vue-good-table>
    <hr>
  </div>
</template>

<script>
export default {
  name: 'BuildYourClinic',
  created () {
    this.viewProducts()
  },
  updated () {
    // this.viewProducts()
  },
  data () {
    return {
      // Component Related Vars:
      alertVariant: '',
      alertMessage: '',
      alertShow: false,

      // database fetched data vars
      // product name
      prodcutTitle: '',
      // product description
      productDescription: '',
      // product image
      productImage: '',
      // price
      productPrice: 0,
      // category options
      productCategory: null,
      productCategoryOptions: {
        4: 'category 1',
        5: 'category 2',
        6: 'category 3'
      },
      // specializations options
      productSpecialization: null,
      productSpecializationOptions: {
        1: 'Hand pieces',
        2: 'Dental units',
        3: 'Compressor',
        4: 'High suction unit',
        5: 'Light care',
        6: 'Scaler (cavitron)',
        7: 'Amalgamator',
        8: 'Xray dental',
        9: 'Panorama xray',
        10: 'Autoclave',
        11: 'Water distlator',
        12: 'Implant motor',
        13: 'Endo Rotary',
        14: 'Apex locator',
        15: 'Loop',
        16: 'Dental microscope',
        17: 'Dental cr',
        18: 'Laser',
        19: 'Accessories',
        20: 'Ultrasound cleaner',
        21: 'Selling machine',
        22: 'Dental lab',
        23: 'Camera store',
        24: 'other'
      },

      productIdEdit: null,
      prodcutTitleEdit: '',
      // product description
      productDescriptionEdit: '',
      // product image
      productImageEdit: '',
      // price
      productPriceEdit: 0,
      // category
      productCategoryEdit: null,
      // specialization
      productSpecializationEdit: null,

      // vue-good-table vars
      columns: [
        { label: 'ID', field: 'id', type: 'number' },
        // images hidden until implemented
        // { label: 'Image', field: 'image', type: 'file', filterOptions: { enabled: false, }, },
        { label: 'Name', field: 'title', type: 'string', filterOptions: { enabled: true } },
        { label: 'Description', field: 'description', type: 'string', filterOptions: { enabled: true } },
        { label: 'Price', field: 'price', type: 'number' },
        { label: 'Category', field: 'category_id', type: 'number' },
        { label: 'Specialization', field: 'specialization_name', type: 'string', },
      ],
      rows: [],
    }
  },
  methods: {
    addProduct () {
      const payload = {
        'title': this.prodcutTitle,
        'description': this.productDescription,
        'image': 'this.productImage',
        'price': this.productPrice,
        'category_id': this.productCategory,
        'specialization_id': this.productSpecialization
      }
      const url = 'https://pizoeg.com/api/v1/store/clinics?token=' + localStorage.pizo_token
      this.$http.post(url, payload)
        .then((response) => {
          console.log(response)
          // handle response:
          if (response.data.error === '0') {
            // this.handleSuccess(response.data.msg)
            this.handleSuccess('Item has been created successfully.')
          } else {
            this.handleError(response.data.msg)
          }
        })
        .catch((error) => {
          this.handleError('Internal Error Happened')
          console.log(error.response)
        })
      // reset vars for alerts work again
      // setTimeout(this.resetCompVars(), 3000)
      // show products after adding one
      setTimeout(this.viewProducts, 2000)
    },

    deleteProduct () {
      var productIDs = this.getSelectedRowsIDs()
      // console.log(productIDs)
      if (productIDs.length > 0) {
        for (var i = 0; i < productIDs.length; i++){
          this.$http.delete('/store/clinics/' + productIDs[i], {
            params: {token: localStorage.pizo_token}
          }).then((response) => {
            this.handleSuccess(response.data.msg)
            this.viewProducts()
          }).catch((error) => {
            this.handleError('some error happened while deleting')
          })
        }
      } else {
        this.handleError('Internal Error Happened')
      }
    },

    editProduct () {
      var payload = {
        "title": this.prodcutTitleEdit,
        "description": this.productDescriptionEdit,
        "image": "image_route",
        "price": this.productPriceEdit,
        "category_id": this.productCategoryEdit,
        "specialization_id": this.productSpecializationEdit
      }
      this.$http.patch('/store/clinics/' + this.productIdEdit + '?token=' + localStorage.pizo_token, payload)
        .then((response) => {
          if (response.data.error === '0') {
            this.handleSuccess(this.response.data.msg)
          } else {
            this.handleError(this.response.data.msg)
          }
        }).catch((error) => {
          console.log(error.response)
          this.handleError('Some Error happened. Try again later.')
        })
      setTimeout(this.viewProducts(), 2000)
    },

    viewProducts () {
      this.$http.get('/store/clinics')
        .then((response) => {
          // console.log(response)
          this.rows = response.data.products
        }).catch((error) => {
          // console.log(error.response)
          // console.log(error.data)
          console.error(error)
          // this.handleError(error)
        })
    },

    getSelectedRowsIDs () {
      var selectedIDs = []
      var selectedRows = this.$refs['productsTable'].selectedRows
      for (var i = 0; i < selectedRows.length; i++) {
        var currentRow = selectedRows[i]
        selectedIDs.push(currentRow['id'])
      }
      return selectedIDs
    },
    showDeleteProductModal () {
      // show delete product modal
      this.$refs.deleteProductModal.show()
    },
    showEditProductModal () {
      // show edit product modal
      this.$refs.editProductModal.show()
    },
    handleDeleteClick () {
      // some checks when clicking delete button
      var selectedIDs = this.getSelectedRowsIDs()
      if (selectedIDs.length > 0) {
        this.showDeleteProductModal()
      } else {
        this.handleError('You must select Items to delete')
      }
    },
    getSelectedProductInfo (productID) {
      // first reset productEdit info
      this.resetProductEditVars()
      // then get to our work
      this.$http.get('/store/clinics/' + productID)
        .then((response) => {
          if (response.data.error === '0') {
            this.prodcutTitleEdit = response.data.product['title']
            this.productDescriptionEdit = response.data.product['description']
            this.productImageEdit = response.data.product['image']
            this.productPriceEdit = response.data.product['price']
            this.productCategoryEdit = response.data.product['category_id']
            this.productSpecializationEdit = response.data.product['specialization_id']
          } else {
            this.handleError('Some Error Happened. Please Try again later!')
          }
        }).catch((error) => {
          this.handleError('Some Error Happened. Please Try again later!')
        })
    },
    handleEditClick () {
      // some checks when clicking edit button
      var selectedIDs = this.getSelectedRowsIDs()
      if (selectedIDs.length === 1) {
        var productId = selectedIDs[0]
        this.productIdEdit = productId
        this.getSelectedProductInfo(this.productIdEdit)
        this.showEditProductModal()
      } else {
        this.handleError('One and only ONE item can be edited at a time.')
      }
    },
    handleModalOkAddProduct () {
      // after pressing OK on AddProduct Modal
      this.addProduct()
    },
    handleModalOkDeleteProduct () {
      // after pressing OK on DeleteProduct Modal
      this.deleteProduct()
    },
    handleModalOkEditProduct () {
      // after pressing OK on EditProduct Modal
      this.editProduct()
    },
    handleSuccess (message) {
      this.alertShow = true
      this.alertVariant = 'success'
      this.alertMessage = message
    },
    handleError (message) {
      this.alertShow = true
      this.alertVariant = 'danger'
      this.alertMessage = message
    },
    resetProductEditVars () {
      this.prodcutTitleEdit = '',
      this.productDescriptionEdit = '',
      this.productImageEdit = '',
      this.productPriceEdit = 0,
      this.productCategoryEdit = null,
      this.productSpecializationEdit = null
    },
    resetCompVars () {
      // reset components variables
      this.alertVariant = ''
      this.alertMessage = ''
      this.alertShow = false
    }
  }
}
</script>

<style scoped>

</style>



