import Vue from 'vue'
import Router from 'vue-router'

// Containers
import AdminFull from '@/containers/AdminFull'

// Views
import Dashboard from '@/views/admin/Dashboard'

import Messages from '@/views/admin/notifications/Messages'
// no logs
// import Logs from '@/views/admin/notifications/Logs'

// all users are under dashboard :3
// import Engineers from '@/views/admin/users/Engineers'
// import Doctors from '@/views/admin/users/Doctors'
// import Companies from '@/views/admin/users/Companies'

import Products from '@/views/admin/market/Products'
import Orders from '@/views/admin/market/Orders'

import Requests from '@/views/admin/maintenance/Requests'

import News from '@/views/admin/misc/News'
import EventsAndCourses from '@/views/admin/misc/EventsAndCourses'
import BuildYourClinic from '@/views/admin/misc/BuildYourClinic'
import Advertisements from '@/views/admin/misc/Advertisements'
import JobOffers from '@/views/admin/misc/JobOffers'

import Login from '@/views/auth/Login'
import Register from '@/views/auth/Register'
import Page404 from '@/views/errors/Page404'
import Page500 from '@/views/errors/Page500'

Vue.use(Router)

export default new Router({
  mode: 'hash',
  linkActiveClass: 'open active',
  scrollBehavior: () => ({ y: 0 }),
  routes: [
    {
      path: '/admin',
      redirect: 'admin/dashboard',
      name: 'Admin',
      component: AdminFull,
      children: [
        {
          path: 'dashboard',
          name: 'Dashboard',
          component: Dashboard
        },
        // Notifications
        {
          path: 'notifications/messages',
          name: 'Messages',
          component: Messages
        },
        // users stuff
        // all users are now under dashboard
        // {
        //   path: 'users/engineers',
        //   name: 'Engineers',
        //   component: Engineers
        // },
        // {
        //   path: 'users/doctors',
        //   name: 'Doctors',
        //   component: Doctors
        // },
        // {
        //   path: 'users/companies',
        //   name: 'Companies',
        //   component: Companies
        // },
        // market stuff
        {
          path: 'market/products',
          name: 'Products',
          component: Products
        },
        {
          path: 'market/orders',
          name: 'Orders',
          component: Orders
        },
        // No logs are needed (should be paied for xD)
        // {
        //   path: 'notifications/logs',
        //   name: 'Logs',
        //   component: Logs
        // },
        // maintenance
        {
          path: 'maintenance/requests',
          name: 'Requests',
          component: Requests
        },
        // misc
        {
          path: 'misc/news',
          name: 'News',
          component: News
        },
        {
          path: 'misc/events-and-courses',
          name: 'EventsAndCourses',
          component: EventsAndCourses
        },
        {
          path: 'misc/build-your-clinic',
          name: 'BuildYourClinic',
          component: BuildYourClinic
        },
        {
          path: 'misc/ads',
          name: 'Advertisements',
          component: Advertisements
        },
        {
          path: 'misc/job-offers',
          name: 'JobOffers',
          component: JobOffers
        }
      ]
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/register',
      name: 'Register',
      component: Register
    },
    {
      path: '/error/404',
      name: '404',
      component: Page404
    },
    {
      path: '/error/500',
      name: '500',
      component: Page500
    }

  ]
})



